import React, { useRef, useState } from 'react';
// Import Swiper React components
import { Swiper, SwiperSlide } from 'swiper/react';

import { Modal } from "./Modal.js";
import { ModalPortal } from "./ModalPortal";


import ReactDOM from 'react-dom'; // ReactDOM import .


// Import Swiper styles
import 'swiper/css';
import 'swiper/css/pagination';
import 'swiper/css/navigation';
import 'swiper/css/scrollbar';

import './Slider.css';

// import required modules
import { Autoplay, Pagination, Navigation, Scrollbar} from 'swiper/modules';


const Slider = () => {

  const [isOpen, setIsOpen] = useState(false);
  function handleOpenModal() {
    document.body.style.overflow = 'hidden';
    setIsOpen(true);
  }

  function handleCloseModal() {
    document.body.style.overflow = 'auto'
    setIsOpen(false);
  }

  

  return (
    <>
      <Swiper
        slidesPerView={1}
        spaceBetween={30}
        loop={true}
        centeredSlides={true}
        pagination={{
          clickable: true,
        }}
        navigation={true}
        scrollbar={{
          hide: true,
        }}
        autoplay={{
        delay: 5000,
        disableOnInteraction: false,
        }}
        modules={[Pagination, Navigation, Scrollbar, Autoplay]}
      
        className="mySwiper"
      >
        
          <SwiperSlide>
            <div className = "slideimg" >
              <img src="https://shouter.ai/materials/play/i/73/47/1734798974263_6482.jpg" onClick={handleOpenModal}/>
              {isOpen && <ModalPortal>
              <Modal onClose={handleCloseModal}/>
              </ModalPortal>}
            </div>
          </SwiperSlide>
          <SwiperSlide><img src="https://shouter.ai/materials/play/i/73/47/1734799148636_8121.jpg"></img></SwiperSlide>
          <SwiperSlide><img src='https://assets.monica.im/ugc-files/image-gen/text_to_image/9eae2330-f337-4ff4-bacd-49ce4621f547/2qXAp1DNgiEkSvlTLWeTXcPxo5F-0x1024.jpeg'>
              </img></SwiperSlide>
          <SwiperSlide><img src="https://shouter.ai/materials/play/i/73/47/1734798952305_4457.jpg"></img></SwiperSlide>
          {/* <SwiperSlide><img src="https://shouter.ai/materials/play/i/73/47/1734799025615_5509.jpg"></img></SwiperSlide>
          <SwiperSlide><img src="https://shouter.ai/materials/play/i/73/47/1734799070955_1414.jpg"></img></SwiperSlide>
          <SwiperSlide><img src="https://shouter.ai/materials/play/i/73/47/1734799109804_6605.jpg"></img></SwiperSlide> */}
        
      </Swiper>
      

    </>
  );
}

export default Slider;
